.container {
  width: 85%;
  max-width: 1622px;
  margin: 0 auto;
  padding: 0 55px;
}
@media (max-width: 995px) {
  .container {
    width: 100%;
    padding: 0 15px;
  }
}
@font-face {
  font-family: "Cuprum-Regular";
  src: url("../fonts/Cuprum-Regular.woff2"), url("../fonts/Cuprum-Regular.woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Cuprum-Bold";
  src: url("../fonts/Cuprum-Bold.woff2"), url("../fonts/Cuprum-Bold.woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Khula-Bold";
  src: url("../fonts/Khula-Bold.woff2"), url("../fonts/Khula-Bold.woff"), url("../fonts/Khula-Bold.ttf");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Khula-Regular";
  src: url("../fonts/Khula-Regular.woff2"), url("../fonts/Khula-Regular.woff"), url("../fonts/Khula-Regular.ttf");
  font-weight: normal;
  font-style: normal;
}
* {
  box-sizing: border-box;
  outline: none;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
html {
  scroll-behavior: smooth;
}
a {
  outline: none;
}
ul {
  margin: 0;
}
body {
  font-family: "Cuprum-Regular", "Arial", sans-serif;
  font-size: 16px;
  font-weight: normal;
  min-width: 320px;
  padding: 0;
  margin: 0;
}
h1,
h2,
h3,
h4,
h5 {
  font-family: "Khula-Bold", "Arial", sans-serif;
}
a {
  text-decoration: none;
  cursor: pointer;
}
input[type="text"]:focus,
input[type="tel"]:focus {
  background: #DFE5EC;
}
input[type="checkbox"] {
  display: none;
}
input[type="checkbox"] + label {
  padding-left: 40px;
  position: relative;
}
input[type="checkbox"] + label::before {
  content: '';
  width: 17px;
  height: 17px;
  background: url("../img/svg/check-off.svg");
  position: absolute;
  left: 0;
  top: 5px;
}
input[type="checkbox"]:checked + label::before {
  background: url("../img/svg/check-on.svg");
}
.faq__see-all,
.new-ads__see-all {
  display: block;
  width: 355px;
  padding: 20px 0;
  text-align: center;
  font-size: 18px;
  line-height: 30px;
  color: #7B8A99;
  background: #DCE8F1;
  border-radius: 8px;
  position: sticky;
  left: 0;
  transition: all 0.3s ease;
}
@media (max-width: 780px) {
  section .section-title {
    font-size: 29px;
    text-align: center;
  }
}
@media (max-width: 549px) {
  section .section-title {
    padding: 0;
  }
}
@media (max-width: 476px) {
  section .section-title {
    font-size: 20px;
  }
}
.scrollToTop-show {
  display: block;
  position: fixed;
  bottom: 40px;
  right: 40px;
  width: 58px;
  height: 58px;
  background: url("../img/svg/arrow-up.svg") no-repeat center, #ADD9F4;
  transition: all 0.2s ease-in;
  border-radius: 50%;
  cursor: pointer;
  z-index: 4;
}
@media (max-width: 995px) {
  .scrollToTop-show {
    bottom: 60px;
    right: 15px;
  }
}
.index-main {
  background: url("../../../img/jpg/body-background.jpg") no-repeat;
  background-size: cover;
  background-attachment: fixed;
  box-shadow: inset 0 -150px #063453;
}
@media (max-width: 995px) {
  .index-main {
    box-shadow: none;
    background: url("../../../img/jpg/body-background.jpg") no-repeat center 0%;
    background-size: contain;
  }
}
.page-header {
  background-color: #063453;
  font-family: "Cuprum-Bold";
  font-size: 18px;
}
.page-header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
}
.page-header__logo {
  height: 45px;
}
.page-header__logo a {
  display: block;
}
.page-header__logo img {
  width: 150px;
  height: 45px;
}
.page-header__user-wrap {
  display: flex;
}
.page-header__language-list {
  display: flex;
  align-items: center;
  list-style: none;
  text-transform: uppercase;
}
.page-header__language-item:not(:last-child) {
  margin-right: 15px;
}
.page-header__language-link {
  color: #77CCF9;
  transition: all 0.3s ease;
}
.page-header__language-link:hover,
.page-header__language-link:focus,
.active-language {
  color: #FFFFFF;
  transition: all 0.3s ease;
}
@media (max-width: 995px) {
  .page-header__logo img {
    width: 106px;
  }
  .page-header__language-link {
    font-size: 16px;
    line-height: 18px;
  }
}
.header-nav {
  margin-left: 60px;
}
.header-nav__list {
  display: flex;
  list-style: none;
  text-transform: uppercase;
  color: #fff;
  padding: 0;
}
.header-nav__item:first-child {
  display: none;
}
.header-nav__link {
  display: flex;
  align-items: center;
  position: relative;
  height: 58px;
  padding: 20px 35px 20px 65px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}
.header-nav__link::before {
  content: '';
  display: block;
  position: absolute;
  left: 30px;
  width: 20px;
  height: 20px;
}
.login-btn:hover,
.login-btn:focus {
  color: #77CCF9;
}
.registration-btn:hover,
.registration-btn:focus {
  color: #77CCF9;
  border: 2px solid #77CCF9;
}
.main-btn::before {
  background: url("../../../img/svg/home.svg") no-repeat;
}
.login-btn::before {
  background: url("../../../img/svg/login.svg") no-repeat;
}
.registration-btn::before {
  background: url("../../../img/svg/user.svg") no-repeat;
}
.registration-btn {
  border: 2px solid #FFFFFF;
}
@media (max-width: 995px) {
  .header-nav {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    margin-left: 0;
    background: #fff;
    text-transform: capitalize;
    z-index: 5;
    box-shadow: 0px 0px 24px rgba(6, 52, 83, 0.1);
  }
  .header-nav__list {
    justify-content: space-between;
  }
  .header-nav__item:first-child {
    display: block;
  }
  .header-nav__item {
    font-size: 16px;
    line-height: 18px;
    color: #063453;
    width: 33.33%;
  }
  .header-nav__item--active {
    font-family: "Khula-Bold", "Arial", sans-serif;
    font-weight: bold;
  }
  .header-nav__link {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    text-align: center;
    padding: 10px 0 5px;
  }
  .header-nav__link::before {
    top: 10px;
    width: 16px;
    height: 16px;
    left: calc(50% - 8px);
  }
  .login-btn::before {
    background: url("../../../img/svg/logintabl.svg") no-repeat;
  }
  .registration-btn::before {
    background: url("../../../img/svg/reg.svg") no-repeat;
  }
  .tablet-container {
    width: 85%;
    margin: auto;
  }
}
.greetings__container {
  display: flex;
  align-items: center;
  min-height: 500px;
  transition: all 1s ease;
  position: relative;
  overflow: hidden;
  background: url("../../../img/svg/ok.svg") no-repeat 85% 1000px, url("../../../img/svg/Ellipse.svg") no-repeat center -1000px;
  background-size: 60%;
}
.greetings__container--onload {
  background: url("../../../img/svg/ok.svg") no-repeat 85%, url("../../../img/svg/Ellipse.svg") no-repeat center;
}
.greetings__title {
  margin: 0 auto;
  width: 61%;
  max-width: 535px;
  font-size: 59px;
  line-height: 78px;
  color: #063453;
  position: relative;
  z-index: 2;
}
@media (max-width: 1250px) {
  .greetings__container--onload {
    min-height: 400px;
    background-size: 60%;
  }
}
@media (max-width: 850px) {
  .greetings__container--onload {
    min-height: 350px;
    background: url("../../../img/svg/ok.svg") no-repeat 75%, url("../../../img/svg/Ellipse.svg") no-repeat top center;
    background-size: 60%;
  }
}
@media (max-width: 780px) {
  .greetings__container--onload {
    min-height: 250px;
    background: url("../../../img/svg/ok.svg") no-repeat 75%, url("../../../img/svg/Ellipse.svg") no-repeat top center;
    background-size: 50%;
  }
  .greetings__title {
    font-size: 35px;
    line-height: 55px;
  }
}
@media (max-width: 630px) {
  .greetings__container--onload {
    min-height: 200px;
    background: url('../../../img/svg/ok.svg') no-repeat 65%, url('../../../img/svg/Ellipse.svg') no-repeat top center;
    background-size: 50%;
  }
}
@media (max-width: 505px) {
  .greetings__title {
    font-size: 18px;
    line-height: 28px;
  }
  .greetings__container--onload {
    min-height: 157px;
  }
}
@media (max-width: 400px) {
  .greetings__container--onload {
    background: url('../../../img/svg/Mobileok.svg') no-repeat 65%, url('../../../img/svg/Mobilellipse.svg') no-repeat top center;
    background-size: 60%;
  }
}
.services {
  display: flex;
  justify-content: space-between;
  padding-top: 80px;
  padding-bottom: 65px;
  background: #FFFFFF;
}
.services__description {
  width: 30%;
}
.services__main-title {
  margin: 0;
  font-size: 49px;
  line-height: 70px;
  color: #3D3D3D;
}
.services__description-text {
  position: relative;
  font-size: 20px;
  line-height: 30px;
  color: #464646;
  text-transform: uppercase;
  padding-bottom: 45px;
}
.services__description-text::after {
  content: '';
  display: block;
  width: 35%;
  height: 3px;
  background: #ADD9F3;
  position: absolute;
  bottom: 0;
}
.services__add-text {
  font-size: 20px;
  line-height: 30px;
  color: #7B8A99;
}
.services__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding: 0;
  width: 62%;
  list-style: none;
}
.services__item {
  display: block;
  width: 155px;
  height: 175px;
  margin-bottom: 45px;
}
.services__link {
  display: block;
  height: 100%;
  padding-top: 130px;
  position: relative;
}
.services__link::before {
  content: '';
  display: block;
  width: 130px;
  height: 130px;
  position: absolute;
  top: 0;
  left: calc(50% - 65px);
  background: url("../../../img/svg/service-ring.svg");
}
.services__link-icon {
  top: calc(50% - 60px);
  left: calc(50% - 37px);
  position: absolute;
}
.services__link-icon {
  transition: all 0.3s ease;
}
.services__link:hover .services__link-icon {
  transform: scale(1.11);
  transition: all 0.3s ease;
}
.services__title {
  font-size: 16px;
  line-height: 30px;
  text-align: center;
  color: #063453;
}
@media (max-width: 995px) {
  .services {
    flex-direction: column;
  }
  .services__main-title {
    text-align: center;
    position: relative;
    width: max-content;
    border-bottom: 3px solid #ADD9F3;
    padding-bottom: 20px;
    margin: auto;
  }
  .services__description,
  .services__list {
    width: 100%;
  }
  .services__description-text,
  .services__add-text {
    display: none;
  }
  .services__list {
    margin-top: 65px;
    justify-content: center;
  }
}
@media (max-width: 549px) {
  .services {
    padding-top: 25px;
    padding-bottom: 0px;
  }
  .services__list {
    margin-top: 35px;
  }
  .services__item {
    height: 125px;
    width: 33.33%;
  }
  .services__link {
    padding-top: 80px;
  }
  .services__link::before {
    width: 80px;
    height: 80px;
    background-size: contain;
    top: 0;
    left: calc(50% - 40px);
  }
  .services__title {
    font-size: 10px;
  }
  .services__link-icon {
    width: 46px;
    left: calc(50% - 23px);
    top: calc(33% - 23px);
  }
}
.about-us {
  padding-top: 40px;
  padding-bottom: 82px;
  background: #fff;
}
.about-us__wrapper {
  display: flex;
  position: relative;
  padding-top: 55px;
}
.about-us__desc-block {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
@keyframes move {
  from {
    transform: rotate(-360deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.about-us__background {
  width: 50%;
  height: max-content;
  position: relative;
}
.about-us__background img {
  width: 100%;
  height: auto;
}
.about-us__background::after {
  content: '';
  display: block;
  width: 40%;
  height: 70%;
  position: absolute;
  right: 40%;
  top: 30%;
  background: url('../../../img/png/about-us-circle-text.png') no-repeat center;
  background-size: contain;
  animation-duration: 10s;
  animation-name: move;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.about-us__title {
  margin: 0;
  font-size: 49px;
  line-height: 70px;
  color: #3D3D3D;
  text-align: center;
  padding-bottom: 45px;
  position: relative;
  margin-bottom: 80px;
}
.about-us__title::after {
  content: '';
  display: block;
  height: 3px;
  width: 86px;
  position: absolute;
  bottom: 0;
  left: calc(50% - 43px);
  background-color: #ADD9F3;
}
.about-us__desc {
  width: 45%;
  font-size: 20px;
  line-height: 30px;
}
.about-us__add-desc {
  display: block;
  width: 100%;
  margin-top: 35px;
}
.about-us__bottom-text {
  width: 60%;
  margin-top: 25px;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  color: #7B8A99;
  margin-bottom: 0;
  margin-top: -120px;
  z-index: 2;
}
.about-us__add-btn {
  display: none;
}
@media (max-width: 1595px) {
  .about-us__bottom-text {
    margin-top: -90px;
  }
}
@media (max-width: 1475px) {
  .about-us__bottom-text {
    margin-top: -45px;
  }
}
@media (max-width: 1435px) {
  .about-us__bottom-text {
    margin-top: 10px;
    width: 100%;
  }
}
@media (max-width: 835px) {
  .about-us__background {
    width: 100%;
  }
  .about-us__desc {
    width: 100%;
  }
  .about-us__add-desc {
    display: none;
  }
  .about-us__title {
    margin-bottom: 0;
  }
  .about-us__add-btn {
    display: inline-block;
    background: transparent;
    border: none;
    color: #7B8A99;
    position: relative;
    padding-right: 30px;
  }
  .about-us__add-btn::after {
    content: '';
    display: block;
    position: absolute;
    right: 0;
    top: 10px;
    width: 16px;
    height: 10px;
    background: url("../../../img/svg/arrow.svg") no-repeat;
  }
}
@media (max-width: 690px) {
  .about-us__title {
    font-size: 29px;
