.accordion {
  background-color: transparent;
  color: #444;
  cursor: pointer;
  padding: 3px 22px 7px 0;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  transition: 0.4s;
  position: relative;
  font-size: 20px;
  line-height: 30px;
}
.accordion p {
  margin: 0;
}
.accordion::after {
  content: '\002B';
  position: absolute;
  color: #777;
  font-weight: bold;
  right: 0;
  top: calc(50% - 13px);
  font-size: 30px;
}
.active:after {
  content: "\2212";
}
.panel {
  padding: 0 18px;
  background-color: white;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
}
